
<template>
    <div>
        <top-nav title="商品分类管理"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1)分类设置为推荐时 则在搜索时展示 推荐分类</p>
            <p>2)最多只能分类到两级</p>
            <p>3)"是否推荐"->设置为推荐之后, 该分类会在首页楼层显示</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" class="search-form" :inline="true">
                    <div>
                        <el-button size="mini" type="danger" @click="createCat(false)">新增分类</el-button>
                    </div>
                </el-form>
                <el-table :data="datas"  size="mini" style="width: 100%">
                    <el-table-column prop="cat_id" label="分类ID"  width="100"></el-table-column>
                    <el-table-column label="分类名称"  header-align >
                        <template slot-scope="scope">
                            <p style="text-indent: 2em" v-if="scope.row.parent_id > 0" >└─{{scope.row.name}}</p>
                            <p v-if="scope.row.parent_id == 0">{{scope.row.name}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column prop="orderby" label="排序">
                        <template slot-scope="scope">
                            <p style="text-indent: 2em" v-if="scope.row.parent_id > 0" >└─{{scope.row.orderby}}</p>
                            <p v-if="scope.row.parent_id == 0">{{scope.row.orderby}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column label="是否显示">
                        <template slot-scope="scope">
                            <span style="text-indent: 2em;display: inline-block"  v-if="scope.row.parent_id != 0">└─</span>
                            <i v-if="scope.row.is_show" class="el-icon-success" style="color: #1593ff" > 是</i>
                            <i v-else class="el-icon-error" style="color: #dcdfe6" > 否</i>
                        </template>
                    </el-table-column>
                    <el-table-column label="是否推荐">
                        <template slot-scope="scope">
                            <span style="text-indent: 2em;display: inline-block"  v-if="scope.row.parent_id != 0">└─</span>
                            <i v-if="scope.row.is_hot" class="el-icon-success"  style="color: #1593ff" > 是</i>
                            <i v-else class="el-icon-error" style="color: #dcdfe6" > 否</i>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                            <span style="text-indent: 2em;display: inline-block"  v-if="scope.row.parent_id != 0">└─</span>
                            <el-button type="text" size="mini" @click="editCat(scope)" >编辑</el-button>
                            <el-popover style="margin: 0 10px;" placement="top" width="160" v-model="scope.row.is_delete">
                                <p style="margin-bottom: 4px">确定删除吗？</p>
                                <div style="text-align: right; margin: 0">
                                    <el-button :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消</el-button>
                                    <el-button :loading="loading" type="primary" size="mini" @click="deleteCat(scope)">确定</el-button>
                                </div>
                                <el-button type="text" slot="reference">删除</el-button>
                            </el-popover>
                        </template>
                    </el-table-column>
                </el-table>
                <br/>
            </div>
        </div>

        <!--编辑新增 弹窗-->
        <el-dialog title="分类"   :modal="false" v-loading="subLoading" :visible.sync="dialog" width="50%" :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small">
                <el-form-item label="分类名称" label-width="80px">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="上级分类" label-width="80px">
                    <el-select v-model="form.parent_id" placeholder="请选择">
                        <el-option :value="0" label="顶级分类">顶级分类</el-option>
                        <el-option v-for="item in datas" v-if="item.parent_id == 0"   :value=item.cat_id :label="item.name" >
                        </el-option>
                    </el-select>
                    <p class="tit"> 1)创建一级分类选择顶级分类</p>
                    <p class="tit"> 2)创建二级分类选择要创建分类的上级分类</p>
                </el-form-item>
                <el-form-item label="是否推荐" label-width="80px">
                    <el-switch v-model="form.is_hot" :value="form.is_hot" :active-value="1" :inactive-value="0"></el-switch>
                </el-form-item>
                <el-form-item label="是否显示" label-width="80px">
                    <el-switch v-model="form.is_show" :value="form.is_show" :active-value="1" :inactive-value="0"></el-switch>
                </el-form-item>
                <el-form-item label="排序" label-width="80px">
                    <el-input v-model="form.orderby" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="封面图片" label-width="80px">
                    <wyu-upload :multi="false" :img="form.image" v-model="form.image"></wyu-upload>
                    <p class="tit"> 1)一级分类建议尺寸265px*90px或等比缩放</p>
                    <p class="tit"> 2)二级分类建议尺寸81px*81px或等比缩放</p>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false">取 消</el-button>
                <el-button size="small" type="primary" @click="createCat(true)">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>
<script>

    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import WyuUpload from "@/components/Photos";

    export default {
        name: 'ProductCategoryIndex',
        components: {TopNav,WyuUpload},
        data() {
            return {
                subLoading: false,
                //整体列表 菊花
                loading: false,
                //列表数据 包含 list count limit
                datas: [],
                nowShow: [],
                //修改 添加 状态控制
                dialog: false,
                //修改 添加 form
                form: {
                    name: '',  // 标题
                    orderby: 0, //排序
                    image: '',
                    is_show: 0,
                    is_hot: 0,
                    parent_id: 0,
                    cat_id: 0,
                },
                categoryId: 0,
            }

        },

        created() {
            this.getCatList();
        },



        methods: {
            /**
             * 获取分类列表
             * @param page {number} 页数
             */
            getCatList() {
                this.loading = true;
                api.post("/shop/admin.goods.category/categoryList", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    let list =  [];
                    res.list.forEach(val => {
                        let child = val.child;
                        delete val.child;
                        list.push(val);
                        child.forEach( v => {
                            list.push(v);
                        });
                    });
                    this.datas = list;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });

            },


            /**
             * 添加分类
             * @param sub {boolean} 是否是 提交操作
             */
            createCat(sub = true) {
                if (sub == false) {
                    this.dialog = true;
                    this.form.name = "";
                    this.form.orderby = "";
                    this.form.parent_id = "";
                    this.form.cat_id = "";
                    this.form.image = "";
                    this.form.is_hot = "";
                    this.form.is_show = "";
                    this.form.sons = this.data5;
                    this.categoryId = 0;
                } else {
                    this.subLoading = true;
                    var url = "/shop/admin.goods.category/categoryCreate";
                    if (this.categoryId > 0) {
                        url = "/shop/admin.goods.category/categoryEdit";
                    }
                    api.post(url, {
                        appToken: this.$store.getters.appToken,
                        name: this.form.name,
                        parent_id: this.form.parent_id,
                        orderby: this.form.orderby,
                        image: this.form.image,
                        is_hot: this.form.is_hot,
                        is_show: this.form.is_show,
                        cat_id: this.categoryId,
                    }).then(res => {
                        this.form = {
                            name: '',  // 标题
                            orderby: 0, //排序
                            image: '',
                            is_show: 0,
                            is_hot: 0,
                            parent_id: 0,
                            cat_id: 0,
                        };
                        this.subLoading = false;
                        this.dialog = false;
                        this.$message({
                            message: '恭喜你，操作成功',
                            type: 'success',
                        });
                        this.getCatList();
                    }).catch((error) => {
                        this.subLoading = false;
                    });
                }
            },
            /**
             * 删除分类
             * @param row {data} 操作的记录数据对象
             */
            deleteCat(scope) {
                this.loading = true;
                api.post("/shop/admin.goods.category/categoryDel", {
                    appToken: this.$store.getters.appToken,
                    cat_id:scope.row.cat_id
                }).then(res => {
                    this.loading = false;
                    this.getCatList();
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                }).catch((error) => {
                    this.loading = false;
                });
            },


            /**
             * 修改分类弹出
             * @param row {data} 操作的记录数据对象
             */
            editCat(scope) {
                this.dialog = true;
                this.form.cat_id = scope.row.cat_id;
                this.categoryId = scope.row.cat_id;
                this.form.orderby = scope.row.orderby;
                this.form.parent_id = scope.row.parent_id;
                this.form.image = scope.row.image;
                this.form.is_hot = scope.row.is_hot;
                this.form.is_show = scope.row.is_show;
                this.form.name = scope.row.name;
                this.form.sons = this.data5;
            },
        }
    };
</script>

